.page {
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  background: linear-gradient(to bottom, #e5e5e5 80%, #fff 20%);
  scroll-behavior: smooth;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }
}
